import * as React from "react"
import { styled, Collapse, IconButton } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore"


// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";

// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKAvatar from "components/MKAvatar";
import MKTypography from "components/MKTypography";

// Images
import profilePicture from "assets/images/bruce-mars.jpg";




function Profile() {
  const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
  })(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  }));
  
  
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <MKBox component="section" py={{ xs: 6, sm: 12 }} bgColor="#fafafa">
      <Container>
        <Grid container item xs={12} justifyContent="center" mx="auto">
          {/* <Button onClick={alert("hello")}> */}
            <MKBox mt={{ xs: 0, md: -25 }} 
            textAlign="center">
              <MKAvatar
                src={profilePicture}
                alt="Anne Hyeyeon"
                sx={{ width: 200, height: 200 }}
                shadow="xl"
              />
            </MKBox>
          {/* </Button> */}
          <Grid container justifyContent="center" py={6}>
            <Grid item xs={12} md={7} mx={{ xs: "auto", sm: 6, md: 1 }}>
              <MKBox display="flex" justifyContent="space-between" alignItems="center" mb={1}>
                <MKTypography variant="h3">Hyeyeon Kim </MKTypography>
              </MKBox>
              <MKBox>
              <MKTypography variant="h6" mb={2}>
                  1992 12 29
                </MKTypography>
                <MKTypography variant="body2">
                   2015.09 ~ 2018.05   - , 
                </MKTypography>
                <MKTypography variant="body2">
                   2018.11 ~ () - ,  B2B  MD
                </MKTypography>
                <MKTypography variant="body2">
                 2022.02 ~ 2022.06   IT    
                </MKTypography>
                  <MKTypography mt={3} variant="h4">
    (click)  
<ExpandMore
          expand={expanded}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >

        <ExpandMoreIcon />
        </ExpandMore>

                  </MKTypography>
                

        <Collapse in={expanded} timeout="auto" unmountOnExit>
        <MKTypography component="span" variant="h6">
                 1 6 :   
                </MKTypography>
        <MKTypography lineHeight={2} mt={2} mb={10} variant="body2" fontWeight="light" color="dark">
        2022 1 6,                 .   1~2     ,  .  2 ,   7    5~6  . <br />     ,           .  ,    .           ,      .  (?),          . <br />      ,        1~6   . 
 
                </MKTypography>
        <MKTypography component="span" variant="h6">
                  :    
                </MKTypography>
        <MKTypography lineHeight={2} mt={2} mb={10} variant="body2" fontWeight="light" color="dark">
          . <br />    ,       ,            .             . <br />
 ,        .      , , ,      .      ,   ''.          ,  . <br />
   ,            .  HTML, CSS             !  , ,           . <br />    .              .    ,      .  
                </MKTypography>
                <MKTypography component="span" variant="h6">
                  MD,     
                </MKTypography>
                <MKTypography lineHeight={2} mt={2} variant="body2" fontWeight="light" color="dark">
           .  B2B  /     ,      . <br /> B2B  ,           . <br /> (ex.    A   10,000 B   15,000  .             ) <br />       ,         ,      . <br />      ,                  .  <br />               . <br />  MD       .  ,       ,        .

</MKTypography>
      </Collapse>
              </MKBox>
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default Profile;
