import React from 'react';
import { styled, Container, Link } from '@mui/material';
import MKBox from 'components/MKBox';
import MenuBookTwoToneIcon from '@mui/icons-material/MenuBookTwoTone';
import MKTypography from 'components/MKTypography';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';

function Testimonials() {
    const HtmlTooltip = styled(({ className, ...props }) => (
        <Tooltip {...props} classes={{ popper: className }} />
      ))(({ theme }) => ({
        [`& .${tooltipClasses.tooltip}`]: {
          backgroundColor: '#f5f5f9',
          color: 'rgba(0, 0, 0, 0.87)',
          maxWidth: 220,
          fontSize: theme.typography.pxToRem(12),
          border: '1px solid #dadde9',
        },
      }));
      
  return (
    <MKBox component="section" py={15} bgColor="#fff">
         <Container maxWidth="lg">
            <MKTypography
            variant="h2"
            sx={{
                fontFamily:'Nanum Myeongjo',
                textAlign: 'center',
                fontSize: 30

            }}>     
        So The last will be first <br />
        </MKTypography>
        <MKTypography
        sx={{
                fontFamily:'Nanum Myeongjo',
                textAlign: 'center',
                fontSize: 15,
                mb:5
            }}
        >
            <MenuBookTwoToneIcon fontSize="small" sx={{ mr:1 }} />
            Matthew 20:16
        </MKTypography> 
        <MKTypography
            variant="body2" 
            sx={{
                textAlign: 'center',

            }}>     
        ,        . <br />           . <br />      ,      .
        </MKTypography>
        <MKTypography
            variant="body2" 
            mt={5}
            sx={{
                textAlign: 'center',

            }}>     
        <Link underline="hover" href="mailto:khy2106@naver.com"> khy2106@naver.com</Link>
        </MKTypography>
        <MKTypography
            variant="body2" 
            mt={1}
            sx={{
                textAlign: 'center',

            }}>     
        <Link underline="hover" href="https://github.com/Anne-Hyeyeon"> https://github.com/Anne-Hyeyeon</Link>
        </MKTypography>
        <MKTypography
            variant="body2" 
            mt={1}
            sx={{
                textAlign: 'center',

            }}>
                <HtmlTooltip
        title={
          <>
               <em> </em>.      ,      .  ,       SNS  !            ()
          </>
        }
      >
       
       <Link underline="hover" href="          https://blog.naver.com/khy2106/"> https://blog.naver.com/khy2106</Link>
      </HtmlTooltip>    
        </MKTypography>
    </Container>
    </MKBox>
  );
}

export default Testimonials;
